<template>
    <van-pull-refresh v-model="isReloading" @refresh="onRefresh">
        <van-list v-model="isLoading" :finished="finished" finished-text="没有更多了" @load="onLoad">
            <div class="list">
                <div
                    class="item"
                    v-for="(item, index) in list"
                    :key="index"
                    :title="item"
                    @click="itemClick(item,index)"
                >
                    <img :src="item.thumb_src">
                    <div>{{item.title}}</div>
                </div>
            </div>
        </van-list>
    </van-pull-refresh>
</template>

<script>
import { ImagePreview } from "vant";
export default {
  data() {
    return {
      origin: "https://api.meizitu.net/",
      headers: {
        Referer: "https://app.mmzztt.com"
      },
      page: 1,
      list: [],
      isReloading: false,
      isLoading: false,
      finished: false
    };
  },
  created() {
    var oMeta = document.createElement("meta");
    oMeta.content = "never";
    oMeta.name = "referrer";
    document.getElementsByTagName("head")[0].appendChild(oMeta);
    this.loadData(1);
  },
  methods: {
    itemClick(item, index) {
      var images = this.list.map(item => item.thumb_src);
      ImagePreview({
        images,
        startPosition: index,
        onClose() {
          // do something
        }
      });
    },
    onRefresh() {
      this.loadData(1);
    },
    onLoad() {
      this.loadData(this.page + 1);
    },
    loadData(page = 1) {
      var url = this.origin + "wp-json/wp/v2/posts?page=" + page;
      return this.request
        .default({
          method: "GET",
          url,
          headers: this.headers
        })
        .then(res => {
          if (res.length == 0) {
            this.finished = true;
          } else {
            this.page = page;
          }
          if (page == 1) {
            this.isReloading = false;
          } else {
            this.isLoading = false;
          }
          this.list = page == 1 ? res : [...this.list, ...res];
        })
        .catch(e => {
          if (page == 1) {
            this.isReloading = false;
          } else {
            this.isLoading = false;
          }
          alert(e);
        });
    }
  }
};
</script>

<style lang="less" scoped>
.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  .item {
    width: 180px;
    img {
      width: 180px;
    }
  }
}
</style>
